import React from 'react'
import PropTypes from 'prop-types'
import Bookmark from '../common/Bookmark'
import QualitiesList from './qualities'
import Table from '../common/table/Table'
import { Link } from 'react-router-dom'
import Profession from './Profession'

const UsersTable = ({ users, onSort, selectedSort }) => {
  const columns = {
    name: {
      path: 'name',
      name: '',
      component: (user) => (
        <Link to={`/users/${user._id}`} className='nav-link'>
          {user.name}
        </Link>
      )
    },
    qualities: {
      name: '',
      component: (user) => <QualitiesList qualities={user.qualities} />
    },
    professions: {
      name: '',
      component: (user) => {
        return <Profession id={user.profession} />
      }
    },
    completedMeetings: { path: 'completedMeetings', name: ', ' },
    rate: { path: 'rate', name: '' },
    bookmark: {
      path: 'bookmark',
      name: '',
      component: (user) => <Bookmark status={user.bookmark} />
    }
  }

  return (
    <div>
      <Table
        onSort={onSort}
        selectedSort={selectedSort}
        columns={columns}
        data={users}
      />
    </div>
  )
}
UsersTable.propTypes = {
  users: PropTypes.array.isRequired,
  onSort: PropTypes.func.isRequired,
  selectedSort: PropTypes.object.isRequired
}

export default UsersTable
